<template>
	<template-auth 
    titulo="Faça o seu login"
    :progressLoader="progressLoader"
    :alert="alertInfo"
    :isBarber="isBarber"
  >
    <form @submit.prevent="logar">
      <div class="form-group">
        <v-text-field
          id="email"
          v-model="email"
          label="E-mail"
          :rules="emailRules"
          v-on:keyup.enter="focusToNext('password')"
          autocomplete="off"
          required 
        />
      </div>

      <div class="form-group">
        <v-text-field
          ref="password"
          v-model="password"
          :type="showPassword ? 'text' : 'password'"
          label="Senha"
          :append-icon="showPassword ? 'visibility_off' : 'remove_red_eye'"
          :rules="passwordRules"
          @click:append="showPassword = !showPassword"
          v-on:keyup.enter="enterClick"
          required 
        />
      </div>

      <div class="text-center mt-4">
        <btn-confirm
          text="Logar"
          :disabled="btnDisabled"
          @clicked="login"
        />
      </div>

			<div class="text-center mt-4">
        <btn-text text="Recuperar senha" @clicked="recoveryPassword" dark />
        <span class="d-block font-weight-light" light>ou</span>
				<btn-secondary
					class="mt-4"
          text="Cadastrar-se"
          @clicked="register()"
        />
      </div>
      <div class="mt-8">
        <btn-text
          text="Voltar"
          icon="chevron_left"
          @clicked="goTo('home')"
          dark
        />
      </div>
    </form>
  </template-auth>
</template>

<script>
import BtnConfirm from '@/components/globais/buttons/confirm/BtnConfirm'
import BtnSecondary from '@/components/globais/buttons/secondary/BtnSecondary'
import BtnText from '@/components/globais/buttons/text/BtnText'
import TemplateAuth from '@/pages/auth/template/Template'
import BarbershopMixin from '@/mixins/BarbershopMixin'
import FormMixin from '@/mixins/FormMixin'
import NavigationMixin from '@/mixins/NavigationMixin'
import UserMixin from '@/mixins/UserMixin'
import ValidationMixin from '@/mixins/ValidationMixin'

export default {
  name: 'Login',
  components: { BtnConfirm, BtnSecondary, BtnText, TemplateAuth },
  mixins: [ BarbershopMixin, FormMixin, NavigationMixin, UserMixin, ValidationMixin ],
  props: {
    progressLoader: { default: false },
    alertInfo: { default: null },
    isBarber: { default: false }
  },
  data: () => ({
    showPassword: false,
    email: '',
    password: ''  
  }),
  computed: {
    btnDisabled () {
      if (!this.isEmailValid(this.email)) return true
      if (!this.isPasswordValid(this.password)) return true
      return false
    }
  },
  mounted () {
    document.querySelector('#email').focus()
  },
  methods: {
    enterClick () {
      if (!this.btnAtivo) this.login()
      else this.setError('Preencha todos os dados corretamente!')
    },
    login () {
      let data = {
        email: this.email,
        password: this.password
      }
      this.$emit('login', data)
    },
    register () {
      if (this.isBarber) this.goTo('register-barber')
      else this.goTo('register')
    },
    recoveryPassword () {
      if (this.isBarber) this.goTo('barber-recovery-password')
      else this.goTo('recovery-password')
    }
  }
}
</script>